import React, { useEffect, useState } from "react";
import Layout from "../components/Layout.jsx";
import Landing from "../components/Landing.jsx";
import Login from "../components/Login.jsx";

const Home = () => {
	//fetching backend data
	const [backendData, setBackendData] = useState([{}]);

	useEffect(() => {
		fetch("/api")
			.then((response) => response.json())
			.then((data) => {
				setBackendData(data);
			});
	});

	if (typeof backendData.users === "undefined") {
		var b = `<div>loading</div>`;
	} else {
		var bd = backendData.users[0];
		var b = `<div>${bd}</div>`;
	}
	console.log(b);

	const [login, setLogin] = useState(0);
	if (login == 1) {
		console.log(login);
		return (
			<Layout title="Home / teamfinder">
				<Landing
					// i think react u cant pass string. It must be object
					onLogin={() => {
						setLogin(1);
					}}
					login={login}
				/>
				<Login
					onSubmit={(s) => {
						console.log("successful login ");
						if (s == 1) {
							setLogin(2);
						}
					}}
				/>
			</Layout>
		);
	} else {
		//0 or 2
		console.log(login);
		return (
			<Layout title="Home / teamfinder">
				<Landing
					onLogin={() => {
						setLogin(1);
					}}
					login={login} //or 0
				/>
			</Layout>
		);
	}
};
export default Home;
